@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
p
{
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    color: #A9A9A9;
}
p a
{
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    color: #A9A9A9;
    margin-left: 0.3rem;
}
.container
{
    margin-top: 5%;
    margin-left: 16.5%;
    margin-right: 20%;
}
.heading
{
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
    color: #4F4F4F;
    margin-bottom: 2rem;
}
.container div
{
    margin-bottom: 2rem;
}
.container .first,.container .second,.container .third,.container .fourth,.container .seventh,.container .eighth
{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    /* grid-gap: 10%; */
}
.container .fifth
{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 10rem;
    gap: 10rem;
}
.container .sixth
{
    display: flex;
    grid-gap: 12.5%;
    gap: 12.5%;
}
@media screen and (max-width:100px)
{
    .container
    {
        margin-left: 1rem;
        margin-right: 1rem;
    }
}
:root
{
    --primary: #2962FF;
    --primary-hover : #0039CB;
    --outline: #FFFFFF;
    --outline-hover : #2962FF;
    --default: #E0E0E0;
    --default-hover : #AEAEAE;
    --secondary: #455A64;
    --secondary-hover : #1C313A;
    --danger: #D32F2F;
    --danger-hover : #9A0007;
    --disableshadow : #3D5AFE;
    --disable : #E0E0E0;
    --text-hover : #2962FF;
    --white: #FFFFFF;
    --black: #000000;
}
.btn
{
    font-family: 'Noto Sans JP' , sans-serif;
    font-size: 14px;
    font-weight: 500;
    border-radius: 6px;
    cursor: pointer;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    border: none;
    background-color: #E0E0E0;
    background-color: var(--default);
    color: #9E9E9E;
}
.btn-default
{
    border: 1px solid inherit;
    color: #000000;
    color: var(--black);
}
.btn-default-hover,.btn:hover,.btn-default-hover:hover
{
    color: #000000;
    color: var(--black);
    background-color: #AEAEAE;
    background-color: var(--default-hover);
}
.btn-primary,.btn-primary-dis,.btn-primary-dis:hover
{
    background-color: #2962FF;
    background-color: var(--primary);
    color: #FFFFFF;
    color: var(--white);
    border: 1px solid #2962FF;
    border: 1px solid var(--primary);
}
.btn-primary:hover,.btn-primary:focus,.btn-primary-hover,.btn-primary-hover:hover
{
    color: #FFFFFF;
    color: var(--white);
    background-color: #0039CB;
    background-color: var(--primary-hover);
    border: 1px solid #0039CB;
    border: 1px solid var(--primary-hover);
}
.btn-outline
{
    background-color: #FFFFFF;
    background-color: var(--outline);
    color: #2962FF;
    color: var(--outline-hover);
    border: 1px solid #2962FF;
    border: 1px solid var(--outline-hover);
} 
.btn-outline:hover,.btn-outline:focus,.btn-outline-hover,.btn-outline-hover:hover
{
    color: #2962FF;
    color: var(--outline-hover);
    background-color: rgba(29, 62, 255, 0.1);
    border: 1px solid #2962FF;
    border: 1px solid var(--outline-hover);
}
.btn-secondary
{
    background-color: #455A64;
    background-color: var(--secondary);
    color: #FFFFFF;
    color: var(--white);
    border: none;
} 
.btn-secondary:hover,.btn-secondary:focus,.btn-secondary-hover,.btn-secondary-hover:hover
{
    color: #FFFFFF;
    color: var(--white);
    background-color: #1C313A;
    background-color: var(--secondary-hover);
}
.btn-danger
{
    background-color: #D32F2F;
    background-color: var(--danger);
    color: #FFFFFF;
    color: var(--white);
    border: none;
} 
.btn-danger:hover,.btn-danger:focus,.btn-danger-hover,.btn-danger-hover:hover
{
    color: #FFFFFF;
    color: var(--white);
    background-color: #9A0007;
    background-color: var(--danger-hover);
}
.btn-primary-icon,.btn-primary-icon:hover
{
    background-color: #2962FF;
    background-color: var(--primary);
    color: #FFFFFF;
    color: var(--white);
    border: 1px solid #2962FF;
    border: 1px solid var(--primary);
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.btn-text
{
    background-color: inherit;
    color: #2962FF;
    color: var(--outline-hover);
    border: none;
} 
.btn-text-hover,.btn-text:hover,.btn-text-hover:hover
{
    color: #2962FF;
    color: var(--outline-hover);
    background-color: rgba(29, 62, 255, 0.1);
    border: none;
}
.btn-disabled-text
{
    background-color: inherit;
    color: #9E9E9E;
}
.btn-primary-outline
{
    background-color: transparent;
    color: #2962FF;
    color: var(--primary);
    border: 2px solid #2962FF;
    border: 2px solid var(--primary);
} 
.btn-danger-outline
{
    background-color: transparent;
    color: #D32F2F;
    color: var(--danger);
    border: 2px solid #D32F2F;
    border: 2px solid var(--danger);
} 
.btn-warning-outline
{
    background-color: transparent;
    color: var(--warning);
    border: 2px solid var(--warning);
} 
.btn-success-outline
{
    background-color: transparent;
    color: var(--success);
    border: 2px solid var(--success);
}
.btn-md
{
    padding: 8px 16px;
    font-size: 14px;
}
.btn-lg
{
    margin-left: -0.5rem;
    padding: 10px 20px;
    font-size: 14px;
}
.btn-sm
{
    padding: 5px 10px;
    font-size: 14px;
}
.hover-text
{
    display: flex;
    justify-content: flex-start;
    font-size: 12px;
    font-family: 'Ubuntu Mono', monospace;
    font-weight: 400;
    color: #828282;
    margin-bottom: 0.5rem;
}
.button-text
{
    display: flex;
    justify-content: flex-start;
    font-size: 12px;
    font-family: 'Ubuntu Mono', monospace;
    font-weight: 400;
    color: #333333;
    margin-bottom: 0.5rem;
}
